import React from 'react';

class SignIn extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            signInEmail: '',
            signInPassword: '',
        }
    }

    onEmailChange = (event) => {
        this.setState({signInEmail: event.target.value})
    }

    onPasswordChange = (event) => {
        this.setState({signInPassword: event.target.value})
    }

    onSubmitSignIn = () => {
        fetch('https://limitless-bastion-51520.herokuapp.com/signin', {
            method: "post",
            headers: {"Content-Type": "application/json"},
            body: JSON.stringify({
                email: this.state.signInEmail, 
                password: this.state.signInPassword
            })
        })
        .then(res => res.json())
        .then(data =>{
            if(data.id){
                this.props.loadUser(data)
                this.props.onRouteChange("home"); 
            }
        })
        
    }

    render(){
        const{ onRouteChange } =  this.props;
        return(
            <article className="br3 ba b--black-10 mv4 w-100 w-75-m mw6 shadow-5 center">
                <main className="pa4 black-80 center">
                    <form className="measure">
                        <fieldset id="sign_up" className="ba b--transparent ph0 mh0 center">
                        <legend className="f1 fw6 ph0 mh0">Sign In</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                            <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password" />
                        </div>
                        </fieldset>
                        <div className="">
                        <input onClick={this.onSubmitSignIn}
                        className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f5 dib" 
                        type="button" value="Sign in" />
                        </div>
                        <div className="lh-copy mt3">
                        <a onClick={() => onRouteChange("register")} href="#0" className="f5 link dim black db">Register</a>
                        </div>
                    </form>
                </main>
            </article>    
        )
    }   
}


export default SignIn;